.table {
  border: 1px solid #eee;
  border-collapse: collapse;
}

.table tr:nth-child(even) { background-color: #f2f2f2; }

.table th, td {
  border-bottom: 1px solid #ddd;
  padding: 5px;
  text-align: left;
}

.table th {
  background-color: #4CAF50;
  color: white;
}



